<template>
    <my-page :loading="isLoading">
        <template #heading>{{ $t('MY_SUBSCRIPTIONS_AND_PURCHASES') }}</template>

        <v-tabs :tabs="tabs">
            <template #subscriptions>
                <p v-if="!subscriptions.length">{{ $t('NO_SUBSCRIPTIONS_MESSAGE') }}</p>
                <div v-else class="subscriptions">
                  <v-subscription
                    v-for="subscription of subscriptions"
                    :data="subscription"
                    :key="subscription.identifier"
                  />
                </div>
                <v-subscription-modal />
            </template>

            <template #purchases>
                purchases
            </template>
        </v-tabs>
    </my-page>
</template>

<script>
import VTabs from '@/components/VTabs.vue';
import MyPage from '../components/MyPage.vue';
import VSubscription from './components/VSubscription.vue';
import VSubscriptionModal from './components/VSubscriptionModal.vue';

export default {
  components: {
    MyPage,
    VTabs,
    VSubscription,
    VSubscriptionModal,
  },
  data() {
    return {
      isLoading: true,
      subscriptions: [],
      purchases: [],
    };
  },
  computed: {
    title() {
      return this.$t('MY_SUBSCRIPTIONS_AND_PURCHASES');
    },
    tabs() {
      return [
        {
          key: 'subscriptions',
          name: this.$t('SUBSCRIPTIONS'),
        },
        {
          key: 'purchases',
          name: this.$t('PURCHASES'),
          disabled: true,
        },
      ];
    },
  },
  methods: {
    async getSubscriptions() {
      try {
        this.subscriptions = await this.$api.getSubscriptions();
      } finally {
        this.isLoading = false;
      }
    },
  },
  created() {
    this.getSubscriptions();
  },
};
</script>

<style lang="scss" scoped>
.subscriptions {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 15px;
}
</style>
