<template>
    <v-modal
        name="subscription"
        class="modal"
        height="auto"
        :width="450"
        close-button
        @before-open="data = $event.params.data"
    >
        <div class="header">
            <app-icon :src="data.icon_uri" size="57px" />

            <div class="header__info">
                <p class="title">
                    {{ $t(data.reseller ? 'SUBSCRIPTION_NAME_WITH_RESELLER' : 'SUBSCRIPTION_NAME', {
                        name: data.name,
                        provider: data.provider,
                        reseller: data.reseller
                    }) }}
                </p>

                <html-with-links :html="data.description" tag="p" class="g-text" />
            </div>
        </div>

        <table class="info">
            <tr>
                <td>
                    {{ $t('STATUS') }}
                </td>
                <td>
                    <span
                        class="status g-status"
                        :class="{
                            'g-status_error': data.status !== 'active',
                            'g-status_success': data.status === 'active'
                        }"
                    >
                        {{ data.status_translated }}
                    </span>
                </td>
            </tr>
            <tr>
                <td class="type">{{ $t('TYPE') }}</td>
                <td>
                    {{ data.type_translated }}
                </td>
            </tr>
            <tr>
                <td>{{ $t('DEVICE') }}</td>
                <td>{{ data.nice_device_model }}</td>
            </tr>
            <tr>
                <td>{{ $t('PRICE') }}</td>
                <td>{{ data.price }}</td>
            </tr>
            <tr>
                <td>
                    {{ expirationLabel }}
                </td>
                <td>
                    <span class="expiration-date">{{ new Date(data.expires_at * 1000).toLocaleDateString() }}</span>

                    <a :href="data.support_uri" target="_blank">{{ $t("GET_SUPPORT") }}</a><template
                        v-if="data.status === 'active'"> | <a :href="data.cancel_uri" @click.prevent="cancelSubscription">{{ $t('CANCEL') }}</a>
                    </template>
                </td>
            </tr>
        </table>
    </v-modal>
</template>

<script>
import AppIcon from '@/components/AppIcon.vue';
import VModal from '@/components/VModal.vue';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';

export default {
  components: {
    AppIcon,
    VModal,
    HtmlWithLinks,
  },
  data() {
    return {
      data: {},
    };
  },
  computed: {
    expirationLabel() {
      if (this.data.status === 'active') {
        return this.$t('SUBSCRIPTION_RENEWS');
      }

      if (Date.now() < new Date(this.data.expires_at * 1000)) {
        return this.$t('SUBSCRIPTION_EXPIRES');
      }

      return this.$t('SUBSCRIPTION_EXPIRED');
    },
  },
  methods: {
    async cancelSubscription() {
      await this.$confirm(this.$t('SUBSCRIPTION_CANCEL_CONFIRMATION'), { cancelText: this.$t('NO'), okText: this.$t('YES_CANCEL') });
      window.location.href = this.data.cancel_uri;
    },
  },
};
</script>

<style lang="scss" scoped>
.modal {
    padding: 15px;
}

.header {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;

    &__info {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
}

.title {
    font-size: 16px;
    font-weight: 500;
    padding-right: 15px;
}

.support {
    position: relative;
    top: 2px;
    left: 1px;
}

.status {
    font-size: 14px;
    word-break: break-word;
}

.status,
.type {
    word-break: break-word;
}

.info {
    margin-top: 10px;

    td {
        padding-right: 10px;
        vertical-align: middle;

        &:first-child {
            font-weight: 500;
        }
    }

    tr:not(:last-child) {
        td {
            padding-bottom: 10px;
        }
    }
}

.expiration-date {
    display: inline-block;
    margin-right: 5px;
}
</style>
